body {
  color: #333;
  background-color: #f8f8f8;
  margin: 0;
  padding: 0;
  font-family: Georgia, Times New Roman, Times, serif;
}

h1, h2 {
  color: #111;
  margin: 0;
  font-weight: normal;
}

a {
  color: inherit;
  text-decoration: none;
}

nav {
  background-color: #fff;
  border-bottom: 1px solid #ddd;
  justify-content: space-between;
  align-items: flex-start;
  padding: 20px;
  display: flex;
  box-shadow: 0 2px 5px #0000001a;
}

.top-left {
  flex-direction: column;
  align-items: flex-start;
  display: flex;
}

.top-left img {
  height: 50px;
  margin-bottom: 10px;
}

.top-left h1 {
  letter-spacing: 1px;
  font-size: 32px;
}

.search-bar {
  align-items: center;
  margin-top: 10px;
  display: flex;
}

.search-bar input[type="text"] {
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-right: 10px;
  padding: 8px;
  font-size: 16px;
}

.search-bar button {
  color: #fff;
  cursor: pointer;
  background-color: #333;
  border: none;
  border-radius: 6px;
  padding: 8px 12px;
  font-size: 16px;
}

.search-bar button:hover {
  background-color: #555;
}

.hamburger {
  cursor: pointer;
  font-size: 24px;
  display: none;
}

.category-bar {
  border-bottom: 1px solid #ddd;
  flex-wrap: wrap;
  justify-content: center;
  padding: 10px;
  display: flex;
}

.category {
  cursor: pointer;
  background-color: #f0f0f0;
  border: none;
  border-radius: 4px;
  margin: 0 12px;
  padding: 10px 15px;
  font-family: Georgia, Times New Roman, Times, serif;
  font-size: 16px;
}

.category:hover {
  background-color: #ddd;
}

#no-results-text {
  color: #d9534f;
  margin: 20px 0;
  font-size: 18px;
  font-weight: bold;
}

.main {
  background-color: #f8f8f8;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 20px;
  padding: 20px;
  display: grid;
}

.headline {
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 4px;
  padding: 15px;
  transition: box-shadow .3s;
  display: block;
}

.headline:hover {
  box-shadow: 0 2px 10px #0000001a;
}

.headline img {
  max-width: 100%;
  height: auto;
  margin-bottom: 15px;
}

.headline h2 {
  color: #111;
  font-size: 22px;
  line-height: 1.4;
}

footer {
  text-align: center;
  color: #fff;
  background-color: #333;
  padding: 20px;
  font-size: 14px;
}

@media (width <= 768px) {
  .top-left h1 {
    font-size: 24px;
  }

  .top-right {
    justify-content: flex-end;
    align-items: center;
    width: 100%;
    display: flex;
  }

  .hamburger {
    display: block;
  }

  .search-bar {
    width: 100%;
  }

  .category-bar {
    z-index: 1000;
    background-color: #fff;
    flex-direction: column;
    width: 100%;
    display: none;
    position: absolute;
    top: 120px;
  }

  .category-bar.active {
    display: flex;
  }
}

@media (width <= 320px) {
  body {
    font-size: 14px;
  }
}

@media (width >= 321px) and (width <= 375px) {
  body {
    font-size: 15px;
  }
}

@media (width >= 376px) and (width <= 414px) {
  body {
    font-size: 16px;
  }
}

@media (width >= 375px) and (height <= 812px) {
  body {
    font-size: 16px;
  }
}

@media (width >= 414px) and (height <= 896px) {
  body {
    font-size: 16px;
  }
}

@media (width >= 411px) and (width <= 412px) {
  body {
    font-size: 16px;
  }
}

@media (width >= 412px) and (height <= 869px) {
  body {
    font-size: 16px;
  }
}

@media (width <= 360px) {
  body {
    font-size: 15px;
  }
}

@media (width <= 412px) {
  body {
    font-size: 16px;
  }
}

@media (width >= 768px) and (width <= 1024px) {
  body {
    font-size: 18px;
  }
}
